<template>
  <div>
    <div class="chatcontent">
      <ul>
        <li v-for="(item, index) in infobox" :key="index">
          <div class="time">{{item.time}}</div>
          <div class="mine_box" v-if ="item.type === 0">
            <div class="imgbox">
              <img :src="imgbox[userInfo.headimg || 0].src" alt class="img" />
            </div>
            <div class="mine_msg">
              <div class="mine_info" v-html="item.content"></div>
            </div>
          </div>
          <div class="other_box" v-else-if ="item.type === 1">
            <div class="imgbox">
              <img :src="imgbox[$route.query.id.headimg  || 0].src" alt class="img" />
            </div>
            <div class="other_msg">
              <div class="other_info" v-html="item.content"></div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="btn_btm">
      <!-- <van-field v-model.trim="info" border @focus="softTe" class="search_box" /> -->
      <van-search v-model.trim="info" shape="round" left-icon="" @focus="softTe" class="search_box" />
      <van-button round color="#00887a" :disabled="info.length == 0" size="mini" class="search_btn" @click="searchbtn">发送</van-button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { Toast } from 'vant'
export default {
  nmae: "chatinfo",
  data() {
    return {
      info: "",
      infobox: [],
      date: '',
      imgbox: [
        { id: 0, src: require("../../../assets/fourth/manf.png") },
        { id: 1, src: require("../../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../../assets/fourth/mans.png") },
        { id: 3, src: require("../../../assets/fourth/womans.png") }
      ]
    };
  },
  computed: {
    ...mapState(["userInfo"])
  },
  methods: {
    ...mapMutations(['clearNewMsg','addNewMsg']),
    searchbtn() {
      var y = new Date().getFullYear() // 2020年
      var m = new Date().getMonth() + 1 // 4月
      var d = new Date().getDate() // 8日
      var h = new Date().getHours() // 14时
      var minutes = new Date().getMinutes() // 16分
      var s = new Date().getSeconds() // 10秒
      var updown = null
      if( h >= 12) {
        updown = '下午'
      } else {
        updown = '早上'
      }
      if( h < 10) {
        h = '0' + h
      }
      if(s < 10) {
        s = '0' + s
      }
      if(h == 0) {
        h = 12
      }
      if(minutes < 10) {
        minutes = '0' + minutes
      }
      if(m < 10) {
        m = '0' + m
      }
      var time = y + '/' + m + '/' + d + ' ' + updown + h + ':' + minutes + ':' + s
      if (this.info.length) {
        this.infobox.push({
          content:this.info,
          time: time,
          tel: this.userInfo.tel,
          histel: this.$route.query.id.tel,
          type: 0 // 0表示自己发送的信息，1表示自己接受到别人发送的信息
        });
        this.$socket.emit('sendMsg', {
          content:this.info,
          time: time,
          tel: this.userInfo.tel,
          histel: this.$route.query.id.tel,
          type: 0
        })
        this.info = ""
        if(this.$route.path == '/chatinfo') {
          this.scrollToBottom();
        }
        localStorage.setItem(this.$route.query.id.tel,JSON.stringify(this.infobox))
        var height = document.getElementsByClassName('chatcontent')[0].style.height
        document.getElementsByClassName('chatcontent')[0].style.height = height - 50 +'px'
      } else {
        Toast('不能输入空值')
      }
    },
    scrollToBottom: function() {
      if(this.$route.path == '/chatinfo') {
        setTimeout(function() {
        var chatlist = document.getElementsByClassName("chatcontent")[0];
        chatlist.scrollTop = chatlist.scrollHeight || 0;
        }, 100);
      }
    },
    softTe() {
      var height = document.getElementsByClassName('chatcontent')[0].style.height
      document.getElementsByClassName('chatcontent')[0].style.height = height - 45 +'px'
    },
    getMsg() {
      this.$socket.removeAllListeners('getMsg');
      // 收到消息的时候
      this.$socket.on('getMsg', res => {
        if( this.$route.path == '/chatinfo' && this.$route.query.id.tel == res.tel ) {
          if(res.histel === this.userInfo.tel ) {
            res.type = 1
            this.infobox.push(res)
            localStorage.setItem(res.tel,JSON.stringify(this.infobox))
            this.scrollToBottom()
          }
        }
      });
      this.$socket.removeAllListeners('NotFriends');
      this.$socket.on('NotFriends', res => {
        if( this.$route.path == '/chatinfo') {
          if(res.tel == this.userInfo.tel ) {
            Toast('他不是你好友，请加好友后联系')
            this.clearNewMsg(res.histel)
            localStorage.removeItem(res.histel)
          }
        }
      });
    },
    init() {
      var info = localStorage.getItem(this.$route.query.id.tel) || []
      if(info.length > 0) {
        var arr = JSON.parse(info)
          if(arr.length > 30) {
            var arrtwo = []
            for(var i = arr.length - 30; i < arr.length; i++) {
              arrtwo.push(arr[i])
            }
            this.infobox = arrtwo
          } else {
            this.infobox = arr
          }
          this.scrollToBottom();
        }
    }
  },
  created() {
    this.clearNewMsg(this.$route.query.id.tel)
    this.getMsg()
    this.init()
  },
};
</script>
<style lang="scss" scoped>
.content_app {
  height: calc(100vh - 46px) !important;
  background-color: #f8f9fb;
}
.chatcontent {
  max-height: calc(100vh - 96px) !important;
  overflow: scroll;
}
.btn_btm {
  height: 40px;
  position: fixed;
  overflow: hidden;
  bottom: 0;
  left:0;
  right:0;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  .search_box {
    width: calc(100vw - 58px);
    padding-right: 5px;
  }
  .search_btn {
    width: 40px;
    height:30px;
    margin-right:12px ;
  }
}
.time{
    text-align: center;
    padding: 10px 0;
    color: gray;
    font-size: 12px;
}
.mine_box {
  float: right;
  width: 100%;
  padding: 10px 0;
  box-sizing: border-box;
  padding-right: 15px;
  .imgbox {
    width: 40px;
    height: 40px;
    float: right;
    .img {
      width: 100%;
      height: 100%;
    }
  }
  .mine_msg {
    float: right;
    margin-right: 10px;
    width: 200px;
    .mine_info {
      box-shadow: lightgrey 0px 0px 5px;
      float: right;
      max-width: 200px;
      font-size: 14px;
      min-height: 16px;
      word-wrap: break-word;
      padding: 10px;
      border-radius: 5px;
      position: relative;
      border: 1px solid white;
      background-color: white;
    }
  }
}
.mine_info:after {
  content: "";
  position: absolute;
  right: -10px;
  top: 15px;
  width: 0;
  height: 0;
  border-style: solid dashed dashed;
  border-color: white transparent transparent;
  overflow: hidden;
  border-width: 10px;
}
.other_box {
  float: left;
  width: 100%;
  padding: 10px 0;
  box-sizing: border-box;
  padding-left: 15px;
  .imgbox {
    width: 40px;
    height: 40px;
    float: left;
    .img {
      width: 100%;
      height: 100%;
    }
  }
  .other_msg {
    float: left;
    margin-left: 10px;
    width: 200px;
    .other_info {
      box-shadow: lightgrey 0px 0px 5px;
      float: left;
      max-width: 200px;
      font-size: 14px;
      min-height: 16px;
      word-wrap: break-word;
      padding: 10px;
      border-radius: 5px;
      position: relative;
      border: 1px solid white;
      background-color: white;
    }
  }
}
.other_info:after {
  content: "";
  position: absolute;
  left: -10px;
  top: 15px;
  width: 0;
  height: 0;
  border-style: solid dashed dashed;
  border-color: white transparent transparent;
  overflow: hidden;
  border-width: 10px;
}
</style>